:root{--ashy: #b7b7a4;--creemy: #f1f0e8;--dark-bottle: #3c3e35;--inner-shadow: inset 0.2rem 0.2rem 0.5rem var(--desert-sand), inset -0.2rem -0.2rem 0.5rem rgb(230, 209, 209)}html{box-sizing:border-box;min-height:100%;position:relative}*,*:before,*:after{box-sizing:inherit;margin:0;padding:0;font-weight:normal;font-family:"Hind Madurai",sans-serif}body{background-color:#f1f0e8;background-color:var(--creemy)}.App{height:100%;display:flex;flex-direction:column;justify-content:center;align-content:center}.btn{align-items:center;background-clip:padding-box;background-color:#5c7b7d;border:1px solid transparent;border-radius:.25rem;box-shadow:rgba(0,0,0,.02) 0 1px 3px 0;box-sizing:border-box;color:#fff;cursor:pointer;display:inline-flex;font-family:system-ui,-apple-system,system-ui,"Helvetica Neue",Helvetica,Arial,sans-serif;font-size:16px;font-weight:600;justify-content:center;line-height:1.25;margin:0;min-height:3rem;padding:calc(.875rem - 1px) calc(1.5rem - 1px);position:relative;text-decoration:none;transition:all 250ms;-ms-user-select:none;user-select:none;-webkit-user-select:none;touch-action:manipulation;vertical-align:baseline;width:13rem;margin-top:2rem;text-decoration:inherit}.btn:hover,.btn:focus{background-color:#5c7b7d;box-shadow:rgba(0,0,0,.1) 0 4px 12px;text-decoration:inherit}.btn:hover{transform:translateY(-1px);text-decoration:inherit}.btn:active{background-color:#c85000;box-shadow:rgba(0,0,0,.06) 0 2px 4px;transform:translateY(0);text-decoration:inherit}.login-form__input{display:flex;flex-direction:column}.login-form__input{margin-top:2rem;max-width:13rem;color:#000;font-size:16px;letter-spacing:.6px;background:none;font-weight:300;border:.2px solid #000;padding:.2rem;caret-color:#000;margin-bottom:1rem;transition:all .25s}.login-form__input:-ms-input-placeholder{opacity:.9;font-weight:200}.login-form__input::placeholder{opacity:.9;font-weight:200}.login-form__input:focus{padding:.2rem;outline:none;font-weight:200}.login-form__input:hover{background-color:rgba(255,255,255,.058)}.hidden{display:none}.nav{padding-left:2%;padding-right:2%;position:-webkit-sticky;position:sticky;font-size:15px;left:0;top:0;z-index:1;background-color:#fff;display:flex;justify-content:space-between;align-items:flex-end;padding-top:1rem;padding-bottom:1rem}.nav__logo{all:unset;font-size:18px;letter-spacing:1px;text-decoration:none;color:#5c7b7d}.nav-logout__btn{background-color:transparent;border:none;letter-spacing:1px;font-size:18px;color:#5c7b7d}.nav-logout__btn:hover{cursor:pointer}.quiz__choices{display:flex;flex-direction:column}.quiz__btn-game{width:100%;margin-top:2rem}.song-timer{text-align:center;padding-top:2rem}.emojis{padding-bottom:1rem}.instruction-wrapper{display:flex;flex-direction:column;align-items:center;padding-top:3rem;padding-left:2rem;padding-right:2rem;text-align:center}.instruction-wrapper__title{padding-bottom:1rem}.instruction-description--title{padding-bottom:1rem}.instruction-description--last{padding-top:1rem}.instruction-description{max-width:25rem;padding-bottom:.5rem}.login-wrapper{padding-left:5%;padding-right:5%;display:flex;flex-direction:column;text-align:center;padding-top:3rem}.login-form{display:flex;flex-direction:column;align-items:center}.login-wrapper__description{padding-top:1rem;padding-bottom:.5rem}.quiz{display:flex;flex-direction:column;justify-content:center;align-self:center;padding-top:3rem;text-align:center}.link{text-decoration:none}.welcome-wrapper{display:flex;flex-direction:column;align-items:center;margin-top:3rem}
